<template>
  <form @submit.prevent="updateSummary">
    <div class="form-group row">
      <label for="new-infected" class="col-sm-3 col-form-label"
        >নতুন আক্রান্ত</label
      >
      <div class="col-sm-9">
        <input
          type="text"
          v-model="form.newInfected"
          class="form-control"
          id="new-infected"
          :disabled="isUpdating"
          placeholder="নতুন আক্রান্তের সংখ্যা লিখুন"
        />
      </div>
    </div>

    <div class="form-group row">
      <label for="total-infected" class="col-sm-3 col-form-label"
        >মোট আক্রান্ত</label
      >
      <div class="col-sm-9">
        <input
          type="text"
          v-model="form.totalInfected"
          class="form-control"
          id="total-infected"
          :disabled="isUpdating"
          placeholder="মোট আক্রান্তের সংখ্যা লিখুন"
        />
      </div>
    </div>

    <div class="form-group row">
      <label for="total-well" class="col-sm-3 col-form-label">সুস্থ্য</label>
      <div class="col-sm-9">
        <input
          type="text"
          v-model="form.totalWell"
          class="form-control"
          id="total-well"
          :disabled="isUpdating"
          placeholder="সুস্থ্যের সংখ্যা লিখুন"
        />
      </div>
    </div>
    <div class="form-group row">
      <label for="total-dead" class="col-sm-3 col-form-label">মৃত্যু</label>
      <div class="col-sm-9">
        <input
          type="text"
          v-model="form.totalDead"
          class="form-control"
          id="total-dead"
          :disabled="isUpdating"
          placeholder="মৃত্যুর সংখ্যা লিখুন"
        />
      </div>
    </div>

    <div class="form-group row">
      <div class="col-sm-9 offset-sm-3">
        <button
          type="submit"
          class="btn btn-success btn-block"
          :disabled="isUpdating || isLoading"
        >
          <div
            v-if="isLoading || isUpdating"
            class="spinner-border spinner-border-sm mb-2"
            role="status"
          >
            <span class="sr-only">Loading</span>
          </div>
          <span v-if="isLoading">Loading...</span>
          <span v-else-if="isUpdating">Updating...</span>
          <span v-else>Update</span>
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";
import { mapState, mapActions } from "vuex";

export default {
  name: "SummaryFormComponent",
  computed: {
    ...mapState(["summary"])
  },
  watch: {
    summary: {
      handler: function(val) {
        if (val != null) {
          this.form = val;
        }
      },
      immediate: true
    }
  },
  data: () => ({
    form: {
      newInfected: null,
      totalInfected: null,
      totalWell: null,
      totalDead: null
    },
    isUpdating: false,
    isLoading: false
  }),
  methods: {
    ...mapActions(["bindSummary"]),
    async updateSummary() {
      // set preloader
      this.isUpdating = true;

      const data = { ...this.form };
      // add current time
      data["updatedAt"] = Timestamp.now();

      try {
        // update data
        await firestore
          .collection("summary")
          .doc("latest")
          .set(data);
      } catch (e) {
        alert("Failed to update. Try again later");
      }

      // hide preloader
      this.isUpdating = false;
    },
    async initSummary() {
      // hide preloader
      this.isLoading = true;
      if (this.summary === null) {
        await this.bindSummary();
      }
      // show preloader
      this.isLoading = false;
    }
  },
  mounted() {
    this.initSummary();
  }
};
</script>

<style scoped></style>
